import {
    Card,
    Col,
    Layout,
    Menu,
    PageHeader,
    Row,
    Tabs,
    Image,
    Drawer,
    Checkbox,
    Form,
    Input,
    Select
} from "antd";
import React, {useCallback, useState} from "react";
import { SettingOutlined, EditOutlined,EllipsisOutlined, DeleteOutlined, FolderAddOutlined, UploadOutlined, MenuUnfoldOutlined, SortAscendingOutlined } from '@ant-design/icons';
import {Link} from "react-router-dom";
const { Content, Sider } = Layout;

const { Meta } = Card;
function getItem(
    label: React.ReactNode,
    key: React.Key,
    path: string,
    icon?: React.ReactNode,
    children?: MenuItem[]
): MenuItem {
    return {
        key,
        icon,
        children,
        label,
        path,
    } as MenuItem;
}

type MenuItem = {
    label: string;
    key: string;
    path: string;
    icon: JSX.Element;
    children?: MenuItem[];
};
const items: MenuItem[] = [
    getItem("Каталог D1", "d1", "/mockup", undefined,[
        getItem("Лобня", "lobnya", "/mockup", undefined, [
            getItem("Парк_1", "park_1", "/mockup"),
            getItem("Парк_2", "park_2", "/mockup"),
            getItem("Стадион", "stadion", "/mockup"),
            getItem("Университет", "university", "/mockup"),
            getItem("Церковь", "cherch", "/mockup"),
        ]),
        getItem("Марк", "8", "/mockup"),
        getItem("Одинцово", "8", "/mockup"),
    ]),
    getItem("Каталог D2", "d2", "/mockup", undefined),
    getItem("Каталог D3", "d3", "/mockup", undefined, ),
    getItem("Видеоролики", "videos", "/mockup", undefined),
    getItem("Пиктограммы", "pictograms", "/mockup", undefined),
    getItem("Удаленные", "deleted", "/mockup", <DeleteOutlined />),
];
const routes = [
    {
        path: 'index',
        breadcrumbName: 'Каталог D1',
    },
    {
        path: 'first',
        breadcrumbName: 'Лобня',
    },
];
const Page: React.FC = () => {
    const [opened, setOpened] = useState<boolean>(false);
    const [collapsed, setCollapsed] = useState<boolean>(false);
    const collapseHandler = useCallback(() => {
        setCollapsed((prev) => !prev);
    }, []);
    return <Layout style={{minHeight: "100vh"}}>
        <Sider collapsible collapsed={collapsed} onCollapse={collapseHandler}>
            <Menu theme="dark" defaultSelectedKeys={["lobnya"]} defaultOpenKeys={["d1", "lobnya"]} mode="inline">
                {items.map((i) => {
                    if (i.children) {
                        return (
                            <Menu.SubMenu title={i.label} key={i.key}>
                                {i.children.map((ch) => {
                                    if (ch.children){
                                        return (
                                            <Menu.SubMenu title={ch.label} key={ch.key}>
                                                {ch.children.map((ch) => {
                                                    return (
                                                        <Menu.Item key={ch.key}>
                                                            <Link to={ch.path}>{ch.label}</Link>
                                                        </Menu.Item>
                                                    );
                                                })}
                                            </Menu.SubMenu>
                                        );
                                    }
                                    return (
                                        <Menu.Item key={ch.key}>
                                            <Link to={ch.path}>{ch.label}</Link>
                                        </Menu.Item>
                                    );
                                })}
                            </Menu.SubMenu>
                        );
                    }
                    return (
                        <Menu.Item key={i.key}>
                            {i.icon && <span style={{marginRight: 5}}>{i.icon}</span>}<Link to={i.path}>{i.label}</Link>
                        </Menu.Item>
                    );
                })}
            </Menu>
        </Sider>
        <Content style={{margin: "0 10px"}}>
            <PageHeader
                title={<div style={{display: "flex", gap: 10}}>
                    <UploadOutlined />
                    <FolderAddOutlined/>
                    <MenuUnfoldOutlined />
                    <SortAscendingOutlined />
                </div>}
                breadcrumb={{routes}}
            />
            <Drawer title="Информация о файле" placement="right" onClose={()=>setOpened(!opened)} visible={opened}>
                <p>Тип: jpeg</p>
                <p>Размер: 1.4mb</p>
                <p>Разрешение: 160x160 (px)</p>
                <p>Создан: 08.07.2002 в 14:01</p>
                <p>Автор: Иванов Иван</p>
                <Form layout="vertical">
                    <Form.Item>
                        <Checkbox checked={true}>POI</Checkbox>
                        <Checkbox checked={false}>Пиктограмма</Checkbox>
                    </Form.Item>
                    <Form.Item label="Наименование RU">
                        <Input value="текст"/>
                    </Form.Item>
                    <Form.Item label="Наименование EN">
                        <Input value='text'/>
                    </Form.Item>
                    <Form.Item label="Описание RU">
                        <Input value='text'/>
                    </Form.Item>
                    <Form.Item label="Описание EN">
                        <Input value='text'/>
                    </Form.Item>
                    <Form.Item label="Тип пиктограммы">
                        <Select/>
                    </Form.Item>
                </Form>
            </Drawer>
            <div className="site-card-wrapper" style={{width: opened ? "calc(100% - 378px)" : "100%"}}>
                <Row gutter={16}>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                    <CardContent setOpened={setOpened} opened={opened}/>
                </Row>
            </div>
        </Content>
    </Layout>
}
export default Page

const CardContent:  React.FC<{setOpened: (v: boolean) => void, opened: boolean}> = ({setOpened,opened}) =>{
    const img = Math.random() > 0.5 ? "https://avatars.mds.yandex.net/get-images-cbir/1530721/CQeJX-bLvZVPFhGWkC6U_w902/ocr" : "https://avatars.mds.yandex.net/get-images-cbir/1597963/v3YONm8D8v_nAcO2tH25jw492/ocr"
    return <Col span={4}>
        <Card
            hoverable
            style={{margin: "5px 0"}}
            cover={
                <Image
                    height={100}
                    src={img}
                />
            }
            actions={[
                <SettingOutlined key="setting" />,
                <EditOutlined key="edit" onClick={()=>setOpened(!opened)}/>,
                <EllipsisOutlined key="ellipsis" />,
            ]}
        >
            <Meta
                title="Card title"
                description="This is the description"
            />
        </Card>
    </Col>
}